<script>
import Vue from "vue";
import SortedTablePlugin from "vue-sorted-table";
import functions from "../../../util/functions.js";
import constants from "../../../util/constants";
import * as VueGoogleMaps from 'vue2-google-maps'
import { gmapApi } from 'vue2-google-maps'
import { mapState } from "vuex";

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyAT9uAsuQPTgLew4c2CmAceEeaLXYeHhsk',
    libraries: 'places',
  },
})
Vue.use(SortedTablePlugin, {
  ascIcon: '<i class="mdi mdi-arrow-down"></i>',
  descIcon: '<i class="mdi mdi-arrow-up"></i>',
});

export default {
  data: function () {
    return {
      constants,
      position: { latitude: 10, longitude: 10 },
      currentAlertId: -1,
      type_agent: "",

      is_doing_action: false,
      action_message: "En cours",

      sortBy: "Nom & prénom",
      perPage: 10,
      currentPage: 1,
      sortDesc: false,
      sortByFormatted: true,
      filterByFormatted: true,
      sortable: true,
      is_loading_articles: true,

      fields: [
        { key: "n", sortable: false },
        { key: "action", sortable: true },

        { key:  `nom`, sortable: true },
        { key:  `postnom`, sortable: true },
        { key:  `prenom`, sortable: true },
        { key:  `sexe`, sortable: true },
        { key: `adresse`, sortable: true },
        { key: `email`, sortable: true },
        { key: `phone`, sortable: true }, 
        { key: `type`, sortable: true }, 
        { key: `service`, sortable: true }, 
        // { key: `date_inscription`, sortable: true }, 

      ],
      utilisateur : {},
      all_articles: [],
      list_categories_articles: [],
      search_text: "",
    };
  },

  async created() {
    try {
      this.utilisateur = JSON.parse(sessionStorage.getItem('utilisateur'));
    } catch(e) {
      this.utilisateur = {id : -1}
    }

    this.doLoadAgents()
  },
  computed: {
    google: gmapApi,
    ...mapState([
      "is_connected",
      "nom_utilisateur",
      "id_utilisateur",
      "est_admin",
    ]),
    rows() {
      return this.all_articles.length;
    },
    agents() {
      return this.all_articles.filter((agent) => {
        if (this.search_text == "") {
          return this.all_articles;
        }
        let attrs = this.fields.map(ff => ff.key).filter(ff => ff != "n" && ff != "action");
        for (let attr of attrs) {
          if (
            agent[attr] != undefined &&
            agent[attr]
              .toLowerCase()
              .toLowerCase()
              .includes(this.search_text.toLowerCase())
          ) {
            return true;
          }
        }
      });
    },

  },
  methods: {
    ...functions,
    async doLoadAgents( ) {
      this.is_loading_articles = true;
      let url = this.constants.api_url + "/usersServices/" + this.utilisateur.service_id;
      let response = await fetch(url, {
        method: "get",
        cache: "no-cache",
        headers: {
          "Content-Type": "application/json"
        },
        // body: JSON.stringify({
        //   user_id: this.id_utilisateur,
        //   user_lat: position.latitude,
        //   user_long: position.longitude,
        // }),
        //credentials: "include",
      });
      let data = await response.json();
      if (data.success) {
        let listTypes = [{ libelle: "Police", id: 1 },
        { libelle: "Police Incendie", id: 2 },
        { libelle: "Service Social", id: 3 },
        { libelle: "Croix rouge", id: 4 },
        ]
        let pos = 0;
        this.all_articles = data.user.map((article) => {
          pos++;
          let n = pos;
          let service = listTypes.find(tt => tt.id == article.service_id)
          if (service)
            service = service.libelle
          else
            service = article.service_id

          return {
            ...article,
            n,
            date_inscription: functions.obtenirDateEtHeureEnFrancais(article.created_at),
            service, 
            action: article.id,
          };
        });


      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "\nMessage : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_loading_articles = false;
    },
    curAlerte() {
      let al = this.all_articles.find(
        (c) => c.id == this.currentAlertId
      );
      if (!al)
        return { longitude: 10, latitude: 10 }
      return {
        ...al,
        longitude: parseFloat(al.longitude),
        latitude: parseFloat(al.latitude)
      }
    },
    doSetCurAlert(id) {
      this.currentAlertId = id
      // alert(id)
    },
    async doDeleteFaq(id_alerte) {
      let res = await this.$swal({
        title: "Supprimer ?",
        text: "Voulez-vous vraiment supprimer cet alerte ?",
        type: "question",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        cancelButtonColor: "#3085d6",
        confirmButtonText: "Supprimer",
        cancelButtonText: "Annuler",
      });
      if (!res.value) {
        return;
      }
      this.action_message = "Suppression  en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/get_my_alerts/" + id_alerte;
      let response = null;
      try {
        response = await fetch(url, {
          method: "delete",
          cache: "no-cache",
          body: JSON.stringify(this.article),
          credentials: "include",
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        this.all_articles = this.all_articles.filter((article) => article.id != id_alerte);
        this.$swal({
          type: "success",
          title: "Effectué",
          text: "La article a été supprimée avec succès. ",
          //footer: '<a href>Why do I have this issue?</a>'
        });
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
    getUserLocalisation(onDone) {
      this.action_message = "Localisation en cours";
      // this.is_doing_action = true;
      this.is_loading_articles = true;

      let onSuccess = (position) => {
        let latitude = position.coords.latitude
        let longitude = position.coords.longitude
        //alert("Latitude : " + latitude +  ".\n Longitude : " + longitude)
        this.is_loading_articles = false;
        if (onDone) {
          onDone({ latitude, longitude })
        }
      }
      let onError = (error) => {
        this.is_loading_articles = false;
        let message = "";
        if (error.code == error.PERMISSION_DENIED) {
          message = "Autorisation refusée"
        } else if (error.code == error.POSITION_UNAVAILABLE) {
          message = "Les informations de localisation sont indisponibles"
        } else if (error.code == error.TIMEOUT) {
          message = "La demande de localisation a expirée"
        } else if (error.code == error.UNKNOWN_ERROR) {
          message = "Une erreur inconnue s'est produite"
        }
        if (message != "") {
          alert(message)
          this.$swal({
            type: "error",
            title: "Erreur",
            text: message,
            //footer: '<a href>Why do I have this issue?</a>'
          });
        }
      }
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(onSuccess, onError)
      } else {
        alert("La géolocalisation est désactivée. Veuillez la réactiver")
        this.$swal({
          type: "error",
          title: "Géolocalisation désactivée",
          text: "La géolocalisation est désactivée. Veuillez la réactiver",
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
    },
  },
};
</script>

<template>
  <section class="mt-5">
    <div class="page-header">
      <h3 class="page-title">Liste des Agents</h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <a href="javascript:void(0);">Agents</a>
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            Liste des Agents
          </li>
        </ol>
      </nav>
    </div>
    <div v-if="!is_loading_articles">

      <div class=" ">
        <div class="card">
          <div class="card-body">
            <!-- <h4 class="card-title mb-5">Liste de Faqs</h4> -->
            <div class="row mb-4 d-flex justify-content-start">
              <div class="col-md-7 justify-content-start">
                <button type="button" @click="$router.push('/admin/agents/nouveau')" :disabled="is_doing_action"
                  class="btn-icon-text d-none btn btn-primary mb-3 mb-md-0">
                  <i class="fa fa-plus btn-icon-prepend"></i>
                  Nouvel Agent
                </button>
              </div>
              <div class="col-md-5 d-none">
                <b-form-group label="" label-for="input5">
                  <b-form-input type="text" v-model="search_text" placeholder="Rechercher"></b-form-input>
                </b-form-group>
              </div>
            </div>

            <div class="table-responsive">
              <b-table :items="agents" id="table-list" responsive :per-page="perPage" :current-page="currentPage"
                :fields="fields" :sort-by.sync="sortBy" :sort-desc.sync="sortDesc">
                <template v-slot:cell(status)="data">
                  <span v-html="data.value"></span>
                </template>
                <template v-slot:cell(action)="data">
                  <div class="d-flex justify-content-center">
                    <button class="btn btn-sm d-none btn-outline-primary py-1 mr-1" title="Modifier un agent" @click="() => $router.push('/admin/agents/editer/' + data.value)
      " :disabled="is_doing_action">
                      <i class="fa fa-pencil"></i>
                    </button>
                    <button class="btn btn-sm btn-outline-primary p-2" title="Voir dans la carte"
                      :disabled="is_doing_action" @mouseenter="() => doSetCurAlert(data.value)" v-b-modal.modalcarte>
                      <i class="fa fa-map"></i>
                    </button>
                  </div>
                </template>
                <template v-slot:cell(profil)="data">
                  <span v-html="data.value"></span>
                </template>
              </b-table>
              <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage" aria-controls="table-list"
                align="right">
              </b-pagination>
            </div>
          </div>
        </div>
      </div>

    </div>
    <div class="mt-5 pt-4" v-else>
      <div class="card">
        <div class="card-body">
          <div class="circle-loader"></div>
          <p class="text-center mt-4">Chargement des données en cours</p>
        </div>
      </div>
    </div>
    <div>
      <div>
        <!-- Le modale de détails de commande -->
        <b-modal id="modalcarte" title="Localisation de l'alerte" size="lg">
          <div v-if="is_doing_action">
            <div class="card mb-4">
              <div class="card-body">
                <div class="circle-loader"></div>
                <p class="text-center mt-4">{{ action_message }}</p>
              </div>
            </div>
          </div>
          <div v-else>
            <div class="card">
              <div class="card-body">
                <h6 class="card-title">Carte</h6>
                <div class="map-container">
                  <p class="my-4"> {{ curAlerte().latitude }}, {{ curAlerte().longitude }} </p>
                  <GmapMap :center="{ lat: curAlerte().latitude, lng: curAlerte().longitude }" :zoom="15" :options="{
                    zoomControl: true,
                    mapTypeControl: true,
                    scaleControl: true,
                    streetViewControl: true,
                    rotateControl: true,
                    fullscreenControl: true,
                    disableDefaultUi: true
                    }" style="width: 100%; height: 500px">
                        <GmapMarker :position="google && new google.maps.LatLng(curAlerte().latitude, curAlerte().longitude)"
                           :clickable="true" :draggable="false" />
                  </GmapMap>
                </div>
              </div>
            </div>
          </div>
        </b-modal>
      </div>
    </div>
  </section>
</template>
